				],
				tabIndex: 0,
				isTabFixed: false,
				introImgList: [
					'https://dimg04.c-ctrip.com/images/300h0i0000009k0kr26D9.jpg',
					'https://dimg04.c-ctrip.com/images/300j0i0000009k152AABF.jpg',
					'https://dimg04.c-ctrip.com/images/300j0i0000009k153BA9E.jpg',
				],
				guide: {
					title: '',
					content: [
						'',
