			}
		}
		.total-price {
			label {
				color: #333;
				font-size: 15px;
				font-weight: bold;
			}
			div {
				display: inline-block;
				width: 150px;
				color: #f90;
				font-weight: bold;
				strong {
					font-size: 30px
				}
			}
			.btn-reserve {
				margin-left: 30px;
				border-radius: 0;
				vertical-align: top;
				font-weight: bold;
			}
		}
		.recommend {
			margin-top: 30px;
			color: #666;
			dt {
				margin: 5px 0;
				font-size: 16px
			}
			dd {
				margin-left: 20px
			}
		}
	}
	.panel {
		margin: 20px 0;
		border: 1px solid #ccc;
		background: #fff;
		.panel-header {
			display: flex;
			justify-content: space-between;
			padding: 10px 15px;
			color: #fff;
			background: #01afc7
		}
		.panel-body {
			padding: 15px
		}
	}
	.additional-service {
		display: flex;
		.service-item {
			display: flex
		}
	}
	.detail-tab {
		width: 1180px;
		padding: 0 15px;
		color: #fff;
		font-size: 15px;
		background: #535d63;
		&.fixed {
			position: fixed;
			top: 0;
			z-index: 99;
		}
		ul {
			display: flex;
			li {
				margin: 0 20px;
				padding: 15px 0;
				color: #fff;
				cursor: pointer;
				&.active {
					color: #75d24d;
					border-bottom: 2px solid #75d24d;
				}
			}
		}
	}
	.tab-panel {
		margin-bottom: 20px;
		.panel-body {
			display: flex;
			padding: 30px 40px 20px 20px;
			background: #fff;
			border: 1px solid #ccc;
			.title {
				width: 60px;
				height: 60px;
				color: #fff;
				padding: 10px;
				font-size: 16px;
				text-align: center;
				background: #75d24d 
			}
			.content {
				flex: 1;
				margin-left: 30px;
				img {
					width: 100%;
					margin-bottom: 10px;
				}
			}
		}
	}
	.ad {
		img {
			width: 100%
		}
	}
</style>


