<template>
	<section>
		<div v-title :data-title="pageTitle"></div>
		<div class="main-wrap">
			<div class="searchbar">
				<div class="filter-item">
					<label></label>
					<el-radio-group v-model="filter.type" @change="handleChange">
				    <el-radio-button v-for="item in typeList" :key="item.label" :label="item.label">{{item.text}}</el-radio-button>
				  </el-radio-group>
			  </div>
			  <div class="filter-item">
					<label></label>
					<el-radio-group v-model="filter.month" @change="handleChange">
				    <el-radio-button v-for="item in monthList" :key="item.label" :label="item.label">{{item.text}}</el-radio-button>
				  </el-radio-group>
			  </div>
			  <div class="filter-item">
					<label></label>
					<el-radio-group v-model="filter.dayNum" @change="handleChange">
				    <el-radio-button v-for="item in dayNumList" :key="item.label" :label="item.label">{{item.text}}</el-radio-button>
				  </el-radio-group>
			  </div>
			</div>
			<el-row :gutter="30" class="m-t m-b">
				<el-col :span="18">
					<el-tabs v-model="activeName" type="card" @tab-click="tabClick">
				    <el-tab-pane label="" name="first">
				    </el-tab-pane>
				    <el-tab-pane label="" name="second"></el-tab-pane>
				    <el-tab-pane label="" name="third"></el-tab-pane>
				    <el-tab-pane label="" name="fourth"></el-tab-pane>
				  </el-tabs>
		  		<div class="ware-list" v-loading="loading">
		    		<el-row :gutter="15" class="ware-item" v-for="(item, index) in wareList" :key="index">
		    			<el-col :span="7" class="ware-img">
			    			<img :src="item.wareImgUrl">
		    			</el-col>
		    			<el-col :span="17" class="ware-detail">
		    				<h3 class="ware-name">
		    					{{item.wareName}}
		    				</h3>
		    				<div class="ware-tag">
		    					<span v-for="(tag, index) in item.wareTag" :key="index">
		    						{{tag}}
		    					</span>
		    				</div>
		    				<div class="ware-date">
		    					<span>{{item.wareDayNum}}</span>
		    					<span>{{item.wareStartDate}}</span>
		    				</div>
		    				<div class="ware-brand">
		    					{{item.wareBrandName}}
		    				</div>
		    				<div class="ware-price">
		    					<i class="fa fa-rmb"></i>
		    					<em>{{item.warePrice}}</em>
		    					<!-- <el-button :plain="true" type="reserve" class="pull-right" @click="btnClick(item.wareId, item.wareName)"></el-button> -->
		    				</div>
		    			</el-col>
		    			<div class="mask" @click="showDetail(item)"></div>
		    		</el-row>
		    	</div>
		    	<div class="pagination">
		    		<el-pagination
				      @current-change="handleCurrentChange"
				      :current-page.sync="pageNo"
				      :page-size="20"
				      layout="prev, pager, next, jumper"
				      :total="200">
				    </el-pagination>
		    	</div>
				</el-col>
				<el-col :span="6">
					<div class="recommend">
						<h3 class="title"></h3>
						<ul class="recommend-list">
							<li class="recommend-item clearfix" v-for="item in recommendList" :key="item.wareId">
								<router-link :to="item.wareUrl" :title="item.wareName">
									<div class="ware-img">
										<img :src="item.wareImgUrl">
									</div>
									<div class="ware-name" >
										{{item.wareName}}
									</div>
								</router-link>
							</li>
						</ul>
					</div>
					<div class="activity">
						<h3 class="title">
							
						</h3>
						<ul class="activity-list">
							<li v-for="item in recommendList" :key="item.wareId" class="activity-item">
								<router-link :to="item.wareUrl" class="ware-img" :title="item.wareName">
									<img :src="item.wareImgUrl">
								</router-link>
							</li>
						</ul>
					</div>
				</el-col>
			</el-row>
		</div>
	</section>
</template>
<script>
	export default {
		data () {
			return {
				pageTitle: '',
				pageNo: 1,
				loading: false,
				filter: {
					type: 0,
					month: 0,
					dayNum: 0,
				},
				typeList: [
					{ label: 0, text: ''},
					{ label: 1, text: ''},
					{ label: 2, text: '+/'},
					{ label: 3, text: '+'},
					{ label: 4, text: '+'},
				],
				monthList: [
					{ label: 0, text: ''},
					{ label: 1, text: ''},
					{ label: 2, text: ''},
					{ label: 3, text: ''},
					{ label: 4, text: ''},
					{ label: 5, text: ''},
					{ label: 6, text: ''},
					{ label: 7, text: ''},
					{ label: 8, text: ''},
					{ label: 9, text: ''},
					{ label: 10, text: ''},
					{ label: 11, text: ''},
					{ label: 12, text: ''},
				],
				dayNumList: [
					{label: 0, text: ''},
					{label: 1, text: '1'},
					{label: 2, text: '2'},
					{label: 3, text: '3'},
					{label: 4, text: '4'},
					{label: 5, text: '5'},
					{label: 6, text: '6'},
					{label: 7, text: '7'},
					{label: 8, text: '8'},
					{label: 9, text: '9'},
					{label: 10, text: '10'},
					{label: 11, text: '10'},
				],
				activeName: 'first',
				wareList: [
					{
						wareId: '100000020170801',
						wareName: '64+++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-9.jpg',
						wareTag: ['', '', ''],
						wareDayNum: 2,
						wareStartDate: 8,
						wareBrandName: '',
						warePrice: 12999,
					},
					{
						wareId: '100000020170802',
						wareName: '21 ++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-6.jpg',
						wareTag: ['', '', ''],
						wareDayNum: 2,
						wareStartDate: 8,
						wareBrandName: '',
						warePrice: 12999,
					},
					{
						wareId: '100000020170803',
						wareName: 'JA21+',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-7.jpg',
						wareTag: ['', ''],
						wareDayNum: 2,
						wareStartDate: 8,
						wareBrandName: '',
						warePrice: 12999,
					},
					{
						wareId: '100000020170804',
						wareName: '21 ++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-10.jpg',
						wareTag: ['', '', ''],
						wareDayNum: 2,
						wareStartDate: 8,
						wareBrandName: '',
						warePrice: 12999,
					},
					{
						wareId: '100000020170805',
						wareName: '21 ++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-3.jpg',
						wareTag: ['', '', ''],
						wareDayNum: 2,
						wareStartDate: 8,
						wareBrandName: '',
						warePrice: 12999,
					},
					{
						wareId: '100000020170806',
						wareName: '21++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-1.jpg',
						wareTag: ['', ''],
						wareDayNum: 2,
						wareStartDate: 8,
						wareBrandName: '',
						warePrice: 12999,
					},
				],
				recommendList: [
					{
						wareId: '2000000001',
						wareUrl: '/detail',
						wareName: '21 5++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-4.jpg',
					},
					{
						wareId: '2000000001',
						wareUrl: '/detail',
						wareName: '21 ++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-5.jpg',
					},
					{
						wareId: '2000000001',
						wareUrl: '/detail',
						wareName: '21 ++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-2.jpg',
					},
					{
						wareId: '2000000001',
						wareUrl: '/detail',
						wareName: '21 ++',
						wareImgUrl: 'http://www.fookvision.com/Public/Wwwfookvisioncom/images/index/box02-8.jpg',
					},
				]
			}
		},
		methods: {
			handleChange(val) {
				this.loading = true;
      	setTimeout(() => {
      		this.wareList.sort(() => {
      			return 0.5 - Math.random()
      		})
      		this.loading = false;
      	}, 500)
			},
			tabClick(tab, event) {
        // console.log(tab)
        this.handleChange()
      },
      showDetail(item) {
      	window.open(`#/detail?wareId=${item.wareId}&&wareName=${item.wareName}`)
      },
      handleCurrentChange(val) {
      	this.handleChange()
      }
		},
		created() {
    	let title = this.$route.query.title || '';
    	this.pageTitle = title ? '-' + title : ''
    }
	}
</script>
<style scoped lang="scss">
	.searchbar {
		margin-bottom: 20px;
		padding: 10px;
		border-bottom: 1px solid #ccc;
	}
	.filter-item {
		padding: 10px 0;
		font-size: 13px;
	}
	.el-radio-button {
		margin-left: 15px
	}
	.el-radio-button__inner {
		border: 1px solid #bfcbd9;
	}
	.ware-list {
		overflow: hidden;
	}
	.ware-item {
		position: relative;
		padding: 15px 10px;
		cursor: pointer;
		border-bottom: 1px solid #f3f3f3;
		&:hover {
			.mask {
				display: block;
			}
		}
		.mask {
			display: none;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			width: 100%;
			background: rgba(0,0,0,.05)
		}
		.ware-img {
			height: 160px;
		} 
		img {
			width: 100%;
			height: 100%
		}
	}
	.ware-detail {
		height: 160px;
		padding: 5px 15px;
		div {
			margin: 8px 0;
			font-size: 12px;
		}
		.ware-name {
			height: 40px;
			margin-bottom: 10px;
			font-weight: bold;
		}
		.ware-tag {
			span {
				margin-right: 15px;
				padding: 2px 6px;
				color: #3aaeff;
				border: 1px solid #3aaeff
			}
		}
		.ware-price {
			margin-bottom: 0;
			color: #f60;
			em {
				font-style: normal;
				font-size: 24px;
			}
			button {
				margin-top: -5px
			}
		}
	}
	.el-button--reserve {
		color: #ff9d00;
		border-radius: 0;
    border: 1px solid #ff9d00;
    &:hover,
    &:active,
    &:focus {
    	color: #fff;
    	background: #ff9d00;
    	border: 1px solid #ff9d00;
    }
	}
	.title {
		margin: 15px 15px 0 10px;
		padding: 10px 0;
	}
	.recommend-item {
		padding: 10px;
		overflow:hidden;
		.ware-img {
			float: left;
			width: 48%;
			height: 80px;
			margin-right: 2%;
		}
		img {
			width: 100%
		}
		.ware-name {
			float: left;
			width: 50%;
			height: 80px;
			font-size: 14px;
			color: #666;
			overflow: hidden
		}
	}
	.activity-item {
		padding: 5px 10px;
	}
	.pagination {
		display: flex;
		justify-content: flex-end;
		padding-top: 20px;
	}
</style>


